main {
  position: relative;
  overflow: hidden;
  z-index: 10;
}

.hide-scroll {
  overflow: hidden;
  height: 100vh;
}

.text-neutral-500 {
  color: #555C6C;
}

.reveal-image {
  position: relative;
  --scaleX: 1;
  --scaleFull: 1.2;
  overflow: hidden;
}
.reveal-image:before {
  background-color: #F7F7F2;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  content: "";
  transform: scaleX(var(--scaleX));
  transform-origin: left;
  transition: 0.5s all ease-out;
  z-index: 10;
}
.reveal-image img {
  transition: 0.5s all ease-out;
  transform: scale(var(--scaleFull));
  width: 100%;
  height: 100%;
  object-fit: cover;
}

